import DashboardLayout from '../layout/DashboardLayout.vue'
// GeneralViews
import NotFound from '../pages/NotFoundPage.vue'

// Admin pages
import Overview from 'src/pages/Overview.vue'
import UserProfile from 'src/pages/UserProfile.vue'
import TaxiInfoAPI from 'src/pages/TaxiInfoAPI.vue'
import MRTInfoAPI from 'src/pages/MRTInfoAPI.vue'
import ElementIcons from 'src/pages/Element/ElementIcons.vue'
const routes = [
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/admin/overview'
  },
  {
    path: '/admin',
    component: DashboardLayout,
    redirect: '/admin/overview',
    children: [
      {
        path: 'overview',
        name: 'Overview',
        component: Overview
      },
      {
        path: 'user',
        name: 'User',
        component: UserProfile
      },
      {
        path: 'TaxiInfoAPI',
        name: 'Taxi Info API',
        component:TaxiInfoAPI
      },
      {
        path: 'MRTInfoAPI',
        name: 'MRT Info API',
        component:MRTInfoAPI
      },
      {
        path: 'ElementIcons',
        name: 'Element Icons',
        component:ElementIcons
      }
    ]
  },
  { path: '*', component: NotFound }
]

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes
